@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');


:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  flex:var(--sidebar-flex);
  background: rgb(238,103,143);
  background: linear-gradient(to bottom, #0066cc 0%, #ccffff 100%);
}
.condiv
{
  padding: 100px ;
  background-color: #f4f5f9;
  flex:var(--condiv-flex);
}

.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  
}
.profilepic
{
  border-radius: 50%;
  width: 200px;
  margin: 30px;
}
.typingeffect
{
  font-size: var(--typing-font);
  margin-bottom: 30px;
}

nav ul{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}



ul {
  list-style-type: none;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
  color: black
  
}
p{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
  width: 100;
  justify-content: space-around;

  
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover
{
  color: #ffffff;
}
.github
{
  margin-right: 20px;
}
.twitter
{
  margin-right: 20px;
}

.myreact-icons {
  color: red;
  height: 2em;
}

.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

.skills-icons
{
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: space-around;
  align-content: center;
  column-gap: 2px;
  row-gap: 20px;
}

.text-icon
{
  display: block;
  font-family: 'Raleway';
  font-size: 15px;
  font-style: var(--fontsize-p);
  font-size:var(--fontsize-nav) ;
  text-align: justify;
}

.projectos
{
  display: grid;
  grid-gap: 130px;
  grid-auto-flow: column;
  grid-template-columns: 186px 186px 186px;
  align-content: center;
}

.projectPic
{
  height: auto; 
    width: auto; 
    max-width: 300px; 
    max-height: 300px;
}

.projectLinks{
  color: black;
  text-decoration: underline;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(to bottom, #0066cc 0%, #ccffff 100%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }

  .skills-icons
{
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: space-around;
  align-content: center;
  column-gap: 10px;
  row-gap: 10px;
}

.text-icon
{
  display: block;
  font-family: 'Raleway';
  font-size: 15px;
  font-style: var(--fontsize-p);
  font-size:var(--fontsize-nav) ;
  text-align: justify;
}

ul {
  list-style-type: none;
}
}